@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&family=Redressed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Redressed&display=swap);
body{margin:0;min-height:100vh}*{box-sizing:border-box;margin:0;padding:0;font-family:"Open Sans", sans-serif;scrollbar-width:thin;scrollbar-color:gray}*::-webkit-scrollbar{width:12px}*::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:10px}*::-webkit-scrollbar-thumb{border-radius:10px;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.5)}

.topbar{display:flex;align-items:center;justify-content:center;height:5rem;font-family:"Redressed", cursive;letter-spacing:1px;font-size:2.5rem;font-weight:600;width:100%;background-color:#1976d2;color:white;box-shadow:rgba(0,0,0,0.2) 0px 2px 4px -1px,rgba(0,0,0,0.14) 0px 4px 5px 0px,rgba(0,0,0,0.12) 0px 1px 10px 0px}@media (max-width: 992px){.topbar{height:4rem;letter-spacing:0px;font-size:2rem}}

.dropdown{width:100%;position:absolute;list-style:none;top:100%;left:0;background-color:white;border:1px solid #e4f2fd;max-height:60vh;overflow-y:scroll}.dropdown>.item{display:block;width:100%;border:none;padding:0.7rem 1.4rem;margin-bottom:0.1rem;background-color:#e4f2fd;transition:all 0.1s ease;text-align:left}.dropdown>.item:hover,.dropdown>.item:focus,.dropdown>.item:active{opacity:0.8;background-color:#e8f5ff}.dropdown>.item>.highlight{background-color:rgba(255,255,0,0.452)}.dropdown>.active{opacity:0.8;background-color:#e8f5ff}

.tooltip{position:relative;-webkit-animation:fadeIn 0.2s ease-in 1;animation:fadeIn 0.2s ease-in 1}.tooltip>.wrapper{position:absolute;padding:0.5rem 1.25rem;background-color:#1c211f;color:white;top:100%;left:9px;transform:translateX(-50%);white-space:nowrap;font-size:0.85rem}.tooltip>.wrapper::before{content:"";position:absolute;left:50%;top:0;transform:translate(-50%, -50%) rotateZ(45deg);width:0.5rem;height:0.5rem;background-color:#1c211f}

.searchbar{background-color:#f2f2f2;padding:0.3rem;position:relative}.searchbar>.wrapper{background-color:white;width:100%;display:flex;justify-content:flex-start;align-items:center;height:3rem}.searchbar>.wrapper>.tagContainer{padding-left:0.25rem;display:flex;align-items:center;max-width:50%;scrollbar-width:none;-ms-overflow-style:none;margin-right:0.6rem;z-index:10000}.searchbar>.wrapper>.tagContainer .searchTag{background-color:#e4f2fd;height:2.4rem;padding:0.5rem 1rem;margin-right:0.6rem;display:flex;flex-wrap:nowrap;align-items:center;justify-content:center;transition:all 0.3s ease;-webkit-animation:fadeIn 0.3s ease-in 1;animation:fadeIn 0.3s ease-in 1}.searchbar>.wrapper>.tagContainer .searchTag:last-child{margin-right:0}.searchbar>.wrapper>.tagContainer .searchTag>span{white-space:nowrap}.searchbar>.wrapper>.tagContainer .searchTag>.icon{position:relative;cursor:pointer;min-width:1rem;max-width:1rem;height:1rem;margin-left:1rem}.searchbar>.wrapper>.tagContainer .searchTag>.icon>img{width:auto;height:100%}.searchbar>.wrapper>.tagContainer .searchTag:hover,.searchbar>.wrapper>.tagContainer .searchTag:focus,.searchbar>.wrapper>.tagContainer .searchTag:active{opacity:0.8;background-color:#e8f5ff}.searchbar>.wrapper>input{flex:1 1;height:100%;margin-right:0.6rem;padding:0.5rem 1rem;font-size:1rem;border:none}.searchbar>.wrapper>input:hover,.searchbar>.wrapper>input:focus,.searchbar>.wrapper>input:active{outline:none;border:none}.searchbar>.wrapper>.icon{cursor:pointer;min-width:1.6rem;height:1.6rem;margin-right:0.5rem}.searchbar>.wrapper>.icon>img{width:auto;height:100%;transition:all 0.2s ease}.searchbar:focus-within{background-color:#e4f2fd}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}

.loader{width:100%;height:4rem;display:flex;align-items:center;justify-content:center}.loader>div{background-color:lightgray;width:0.5rem;height:0.5rem;border-radius:50%;margin-right:1rem}.loader>div:nth-child(1){-webkit-animation:resize 1.2s linear 0s infinite;animation:resize 1.2s linear 0s infinite}.loader>div:nth-child(2){-webkit-animation:resize 1.2s linear 0.4s infinite;animation:resize 1.2s linear 0.4s infinite}.loader>div:nth-child(3){-webkit-animation:resize 1.2s linear 0.8s infinite;animation:resize 1.2s linear 0.8s infinite}@-webkit-keyframes resize{0%{width:0.1rem;height:0.1rem}25%{width:0.5rem;height:0.5rem}25%{width:1rem;height:1rem}75%{width:0.5rem;height:0.5rem}100%{width:0.1rem;height:0.1rem}}@keyframes resize{0%{width:0.1rem;height:0.1rem}25%{width:0.5rem;height:0.5rem}25%{width:1rem;height:1rem}75%{width:0.5rem;height:0.5rem}100%{width:0.1rem;height:0.1rem}}

.views{padding:1rem 0;display:grid;grid-template-columns:1fr 1fr;grid-column-gap:1rem;max-height:40rem;height:75%;z-index:1}.views .item{display:flex;align-items:center;justify-content:space-between;padding:0.6rem 2.25rem;transition:all 0.2s ease;cursor:pointer}.views .item>.icon{min-width:1.25rem;max-width:1.25rem;height:1.25rem;cursor:pointer}.views .item>.icon>img{width:100%;height:100%}.views .item:hover{background-color:#dfdddd}.views .leftView,.views .rightView{padding-top:0.5rem;padding-left:0.5rem;background-color:#f2f2f2;overflow-y:scroll}

.index{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.index>.contentSpace{flex:1 1;height:100%;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;margin-top:4rem}.index>.contentSpace>.content{width:90%;max-width:65rem;height:50rem;margin:auto}

.App{width:100%;min-height:100vh}

