.dropdown {
	width: 100%;
	position: absolute;
	list-style: none;
	top: 100%;
	left: 0;
	background-color: white;
	border: 1px solid #e4f2fd;
	max-height: 60vh;
	overflow-y: scroll;

	> .item {
		display: block;
		width: 100%;
		border: none;
		padding: 0.7rem 1.4rem;
		margin-bottom: 0.1rem;
		background-color: #e4f2fd;
		transition: all 0.1s ease;
		text-align: left;

		&:hover,
		&:focus,
		&:active {
			opacity: 0.8;
			background-color: #e8f5ff;
		}

		> .highlight {
			background-color: rgba(255, 255, 0, 0.452);
		}
	}

	> .active {
		opacity: 0.8;
		background-color: #e8f5ff;
	}
}
