.loader {
	width: 100%;
	height: 4rem;
	display: flex;
	align-items: center;
	justify-content: center;

	> div {
		background-color: lightgray;
		width: 0.5rem;
		height: 0.5rem;
		border-radius: 50%;
		margin-right: 1rem;

		&:nth-child(1) {
			animation: resize 1.2s linear 0s infinite;
			// animation-delay: 0s;
		}
		&:nth-child(2) {
			animation: resize 1.2s linear 0.4s infinite;
			// animation-delay: 0.25s;
		}
		&:nth-child(3) {
			animation: resize 1.2s linear 0.8s infinite;
			// animation-delay: 0.5s;
		}
	}
}

@keyframes resize {
	0% {
		width: 0.1rem;
		height: 0.1rem;
	}
	25% {
		width: 0.5rem;
		height: 0.5rem;
	}
	25% {
		width: 1rem;
		height: 1rem;
	}
	75% {
		width: 0.5rem;
		height: 0.5rem;
	}
	100% {
		width: 0.1rem;
		height: 0.1rem;
	}
}
