.index {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	> .contentSpace {
		flex: 1;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin-top: 4rem;

		> .content {
			width: 90%;
			max-width: 65rem;
			height: 50rem;
			margin: auto;
		}
	}
}
