.views {
	padding: 1rem 0;
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-column-gap: 1rem;
	max-height: 40rem;
	height: 75%;
	z-index: 1;

	.item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0.6rem 2.25rem;
		transition: all 0.2s ease;
		cursor: pointer;

		> .icon {
			min-width: 1.25rem;
			max-width: 1.25rem;
			height: 1.25rem;
			cursor: pointer;

			> img {
				width: 100%;
				height: 100%;
			}
		}

		&:hover {
			background-color: #dfdddd;
		}
	}

	.leftView,
	.rightView {
		padding-top: 0.5rem;
		padding-left: 0.5rem;
		background-color: #f2f2f2;
		overflow-y: scroll;
	}
}
