.searchbar {
	background-color: #f2f2f2;
	padding: 0.3rem;
	position: relative;

	> .wrapper {
		background-color: white;
		width: 100%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		height: 3rem;

		> .tagContainer {
			padding-left: 0.25rem;
			display: flex;
			align-items: center;
			max-width: 50%;
			// overflow-x: scroll;
			// overflow-y: auto;
			scrollbar-width: none;
			-ms-overflow-style: none;
			margin-right: 0.6rem;
			z-index: 10000;

			.searchTag {
				background-color: #e4f2fd;
				height: 2.4rem;
				padding: 0.5rem 1rem;
				margin-right: 0.6rem;
				display: flex;
				flex-wrap: nowrap;
				align-items: center;
				justify-content: center;
				transition: all 0.3s ease;
				animation: fadeIn 0.3s ease-in 1;
				// overflow: visible;

				&:last-child {
					margin-right: 0;
				}

				> span {
					white-space: nowrap;
				}

				> .icon {
					position: relative;
					cursor: pointer;
					min-width: 1rem;
					max-width: 1rem;
					height: 1rem;
					margin-left: 1rem;

					> img {
						width: auto;
						height: 100%;
					}
				}

				&:hover,
				&:focus,
				&:active {
					opacity: 0.8;
					background-color: #e8f5ff;
				}
			}
		}

		> input {
			flex: 1;
			height: 100%;
			margin-right: 0.6rem;
			padding: 0.5rem 1rem;
			font-size: 1rem;
			border: none;

			&:hover,
			&:focus,
			&:active {
				outline: none;
				border: none;
			}
		}

		> .icon {
			cursor: pointer;
			min-width: 1.6rem;
			height: 1.6rem;
			margin-right: 0.5rem;

			> img {
				width: auto;
				height: 100%;
				transition: all 0.2s ease;
			}
		}
	}

	&:focus-within {
		background-color: #e4f2fd;
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
