.tooltip {
	position: relative;
	animation: fadeIn 0.2s ease-in 1;

	> .wrapper {
		position: absolute;
		padding: 0.5rem 1.25rem;
		background-color: #1c211f;
		color: white;
		top: 100%;
		left: 9px;
		transform: translateX(-50%);
		white-space: nowrap;
		font-size: 0.85rem;

		&::before {
			content: "";
			position: absolute;
			left: 50%;
			top: 0;
			transform: translate(-50%, -50%) rotateZ(45deg);
			width: 0.5rem;
			height: 0.5rem;
			background-color: #1c211f;
		}
	}
}
